import { Component, OnInit } from '@angular/core';
import { ProductService } from './../_services/product.service';
import { ActivatedRoute } from '@angular/router';
import { AppSettings } from '../app.constant';
import { element } from 'protractor';
import { LocalStorageService } from '../_services';
import { FacebookService, InitParams } from 'ngx-facebook';
declare var jquery: any;
declare var $: any;
declare var CKEDITOR: any;

@Component({
	selector: 'app-product-detail',
	templateUrl: './product-detail.component.html',
	styleUrls: ['./product-detail.component.css']
})
export class ProductDetailComponent implements OnInit {

	constructor(private productService: ProductService, private route: ActivatedRoute,
		private storageService: LocalStorageService, private fb: FacebookService) {
		// facebook
		let initParams: InitParams = {
			appId: '1848389655171361',
			xfbml: true,
			version: 'v2.11'
		};

		fb.init(initParams);
	}

	productId: string = '';
	product: any = {};
	currentSize: any = {};
	currentImage: any = {};
	colors: any = [];
	currentQuantity: number = 1;
	currentUrl = window.location.href;
	slider: any;

	tabs: any = [
		{
			header: 'description_tab',
			content: 'description_tab'
		},
		{
			header: 'comment_tab',
			content: 'comment_tab'
		}
	];

	ngOnInit() {
		this.productId = this.route.snapshot.params['id'];
		if (this.productId === '') {
			// thông báo lỗi ở đây
		}
		else {
			this.getProductDetail(this.productId);
		}
	}

	getProductDetail(id: string) {
		this.productService.getById(id).subscribe(res => {
			this.product = res;
			this.product.ProductImages.forEach(imgElement => {
				imgElement.Path = AppSettings.API_ENDPOINT_IMAGE + imgElement.Path.substr(1);
			});
			console.log(this.product);
			this.currentImage = this.product.ProductImages[0];
			this.currentSize = this.product.ProductSizeModels[0];
			this.colors = this.product.Colors.split(',');
			this.showDescription();
		}, err => {

		});
	}

	showDescription() {
		this.product.Description = $('<div/>').html(this.product.Description).text();
		this.product.Description.replace(/\"/g, "'");
		this.product.Description.replace(/(\r\n|\n|\r)/gm, " ") + '"';
		var content = this.product.Description;
		setTimeout(function () {
			$('#DescEditor').html(content);
		}, 1000);
		this.initControlSlider();
	}

	initControlSlider() {
		var slider = $('#bxslider').bxSlider({
			maxSlides: 5,
			minSlides: 5,
			auto: true,
			slideWidth: 240,
			pager: false
		});
		$('.owl-next').click(function () {
			slider.goToNextSlide();
			return false;
		});

		$('.owl-prev').click(function () {
			slider.goToPrevSlide();
			return false;
		});
	}

	chooseImageView(image) {
		this.currentImage = image;
	}

	showColor(color) {
		this.colors.forEach((element, index) => {
			$('#color-' + index).css('background-color', element + '');
		});
	}

	plusOneToCart(quantity) {
		if (quantity < 0) {
			if (this.currentQuantity > 1) {
				this.currentQuantity += quantity;
			}
		} else {
			this.currentQuantity += quantity;
		}
	}

	addToCart() {
		this.storageService.addNewItem(this.productId, this.product.Name, this.currentQuantity, this.product.ProductImages[0].Path, this.currentSize.Price);
	}

	chooseTab(tab) {
		this.tabs.forEach(element => {
			$('.' + element.header).removeClass('active');
			$('#' + element.content).hide();
		});
		$('.' + tab).addClass('active');
		$('#' + tab).show();
	}

}



