import { Component } from '@angular/core';
import { LocalStorageService, CategoryService } from './_services';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export class AppComponent {
	title = 'app';
	cart: any;
	subscription: Subscription;

	menus: any = [];

	constructor(private storageService: LocalStorageService, private categoryService: CategoryService) {
		// this.cart.Name = ''
		this.cart = JSON.parse(localStorage.getItem("Cart"));
		this.subscription = this.storageService.watchStorage().subscribe(cart => {
			this.cart = cart;
			console.log(this.cart)
			this.cart = JSON.parse(localStorage.getItem("Cart"));
		});
	}

	ngOnInit() {
		this.getMenus();
	}

	getMenus() {
		this.categoryService.GetAllGroupForMenu().subscribe(res => {
			this.menus = res;
		});
	}

	ngOnDestroy() {
		// unsubscribe to ensure no memory leaks
		this.subscription.unsubscribe();
	}

	removeItemCart(id: string) {
		this.storageService.removeItem(id);
	}
}



