import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable, } from 'rxjs/Observable';

import { AppSettings } from '../app.constant';
import { Subject } from 'rxjs/Rx';

@Injectable()
export class LocalStorageService {
    constructor(private http: Http) { }

    private cart = new Subject<any>();

    addNewItem(id: string, name: string, quantity: number, image: string, price: any) {
        var newItem = {
            Id: id,
            Name: name,
            Quantity: quantity,
            Image: image,
            Price: price
        };
        var currentCart = JSON.parse(localStorage.getItem("Cart"));
        if (currentCart == undefined) {
            var cart = {
                subtotal: price * quantity,
                listProduct: [newItem]
            }
            localStorage.setItem('Cart', JSON.stringify(cart));
        } else {
            /// check same product
            var flag = false;
            var subtotal = 0;
            currentCart.listProduct.forEach(element => {
                if(element.Id == id){
                    element.Quantity += quantity;
                    flag = true;
                }
            });
            if(!flag){
                currentCart.listProduct.push(newItem);
            }
            currentCart.listProduct.forEach(element => {
                subtotal += element.Price * element.Quantity;
            });
            var newCart = {
                subtotal: subtotal,
                listProduct: currentCart.listProduct
            }
            console.log(newCart);
            localStorage.setItem('Cart', JSON.stringify(newCart));
        }
        // localStorage.setItem('Cart', JSON.stringify(item));
        this.cart.next();
    }

    removeItem(id: string){
        var currentCart = JSON.parse(localStorage.getItem("Cart"));
        var subtotal = 0;
        currentCart.listProduct.forEach(function(element, index){
            if(element.Id == id){
                currentCart.listProduct.splice(index, 1);
            }
        });

        currentCart.listProduct.forEach(element => {
            subtotal += element.Price * element.Quantity;
        });
        var newCart = {
            subtotal: subtotal,
            listProduct: currentCart.listProduct
        }
        localStorage.setItem('Cart', JSON.stringify(newCart));
        this.cart.next();
    }

    watchStorage(): Observable<any> {
        return this.cart.asObservable();
    }

}


