import { Component, OnInit } from '@angular/core';

import { LocalStorageService, ProductService, CategoryService } from '../_services/index';
import { AppSettings } from '../app.constant';
import { Router } from '@angular/router';
declare var $: any;
@Component({
	selector: 'app-home',
	templateUrl: './home.component.html',
	styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
	count: any = 1;
	menus: any = [];
	categories: any = [];
	top3Products: any = [];
	constructor(private storageService: LocalStorageService, private router: Router,
		private categoryService: CategoryService, private productService: ProductService) { }

	ngOnInit() {
		this.setCategories();
	}

	addToCart() {
		this.storageService.addNewItem('abcdef' + this.count, 'Libero isum default', 1, 'http://demo.theme-sky.com/gon-supermarket/wp-content/uploads/2015/05/15.jpg', 345);
		this.count += 1;
	}

	chooseCategory(grId, cateId) {
		var gr = this.findIndexGroup(grId);
		this.categories[gr].isLoading = true;
		this.productService.get6Products(cateId).subscribe(products => {
			products.forEach(proElement => {
				proElement.ProductImages.forEach(imgElement => {
					imgElement.Path = AppSettings.API_ENDPOINT_IMAGE + imgElement.Path.substr(1);
				});
			});
			this.categories[gr].isLoading = false;
			this.categories[gr].listProducts = products;
		});
		this.categories.forEach(element => {
			if (element.id == grId) {
				element.listCates.forEach(category => {
					if (category.Id == cateId) {
						category.active = 'current';
					} else {
						category.active = '';
					}
				});
			}
		});
	}

	setCategories() {
		this.productService.getDataHomePage().subscribe(res => {
			this.top3Products = AppSettings.convertImagesProduct(res.Top3Products);
			// $('#products-slider').bxSlider({
			// 	moveSlides: 1,
			// 	minSlides: 1,
			// 	maxSlides: 1
			// });
			this.menus = res.GroupCategory;
			this.convertDataHome(res.GroupCategory);
		});
	}

	findIndexGroup(id) {
		var index = -1;
		this.categories.forEach((element, i) => {
			if (element.id == id) {
				index = i;
			}
		});
		return index;
	}

	convertDataHome(res: any) {
		res.forEach(element => {
			var group = {
				id: element.Id,
				Name: element.Name,
				listCates: [],
				listProducts: [],
				isLoading: false
			};
			var cates = [];
			var products = [];
			element.Categories.forEach((cateElement, index) => {
				cateElement.active = cateElement.Current == 'current' ? 'current' : '';
				cates.push(cateElement);
				if (index == 0) {
					cateElement.Products.forEach(proElement => {
						proElement.ProductImages.forEach(imgElement => {
							imgElement.Path = AppSettings.API_ENDPOINT_IMAGE + imgElement.Path.substr(1);
						});
						products.push(proElement);
					});
				}
			});
			group.listProducts = products;
			group.listCates = cates;
			if (element.Categories.length != 0) {
				this.categories.push(group);
			}
		});

	}

	viewProductDetail(id: string) {
		console.log(id);
		this.router.navigateByUrl('/product-detail/' + id);
	}

}



